@import '../../styles/var.sass';

@mixin active {
  background-color: $green;
  color: $icon-white;
  .svg {
    fill: $icon-white; } }

.block_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  @media (min-width: 992px) {
 }    // padding: 0px 15%
  .img_block {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 45%;
    height: 35vw;
    text-align: center;
    margin: 20px 0px;
    padding: 10px;
    background-color: $gray;
    border-radius: 10px;
    cursor: pointer;
    box-sizing: border-box;
    @media (min-width: 992px) {
      flex-basis: 25%;
      height: 10vw;
      margin: 20px 20px; }
    .svg {
      width: 60px;
      height: 60px;
      fill: $icon-black; }
    .img_text {
      font-size: 0.85rem;
      margin-top: 1vw;
      @media (min-width: 992px) {
        font-size: 1.2rem; } }
    &:hover,
    &.active {
      @include active; } } }
.body_actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  .button {
    display: block; } }
