@import './var.sass';

.btn-primary {
  background-color: $green;
  border-color: $green;
  &:hover,
  &:not(:disabled):not(.disabled):active {
    background-color: $green;
    border-color: $green; } }
.btn-outline-primary {
  background-color: $light-green;
  border-color: $light-green;
  color: $icon-black;
  &:hover,
  &:not(:disabled):not(.disabled):active {
    background-color: $light-green;
    border-color: $light-green;
    color: $icon-black; }
  &:focus {
    box-shadow: none; } }

.col {
  padding: 0px; }
.row {
  margin: 0px; }
